import React, {Component} from 'react';
import Navigation from '../../../reuseables/navigation';
import Steps from '../../../reuseables/steps';
import { Container, Box, Row, Column, Button, ButtonSecondary } from '../../../styles/init';
import Form from '../../../reuseables/form';

export default class StepOne extends Component {
    constructor() {
        super()
        this.state = {
            data: {
                make: "",
                model: "",
                series: "",
                year: "",
                milleage: "",
                bodytype: "",
                transtype: "",
                condition: "",
                price: "",
               pricenegotiable: false,
            },
            disabled: true           
        }
      }

      checkValidity = () => {
        let valid = [];
        Object.keys(this.state.data).map(key => {
            if(key !== "pricenegotiable"){
                this.state.data[key] ? valid.push(true) : valid.push(false);
            }
        });
        const isvalid = valid.every(item => item);
        isvalid ?  this.setState({disabled: false}) : this.setState({disabled: true});
      }

    getMakeData = (value,valid) => { 
        this.setState({
            data: {
                ...this.state.data,
                make: value
            }  
        },() => {
            this.checkValidity()
        })   
    }
  
    getModelData = (value,valid) => { 
        this.setState({
            data: {
                ...this.state.data,
                model: value
            }  
        },() => {
            this.checkValidity()
        })
  }
  
    getSeriesData = (value,valid) => { 
        this.setState({
            data: {
                ...this.state.data,
                series: value
            }  
        },() => {
            this.checkValidity()
        })
    }
  
    getYearData = (value,valid) => { 
        this.setState({
            data: {
                ...this.state.data,
                year: value
            }  
        },() => {
            this.checkValidity()
        })
    }
  
    getMilleageData = (value,valid) => { 
        this.setState({
            data: {
                ...this.state.data,
                milleage: value
            }  
        },() => {
            this.checkValidity()
        })
    }
  
    getBodyTypeData = (value,valid) => { 
        this.setState({
            data: {
                ...this.state.data,
                bodytype: value
            }  
        },() => {
            this.checkValidity()
        })
    }
  
    getTransTypeData = (value,valid) => { 
        this.setState({
            data: {
                ...this.state.data,
                transtype: value
            }  
        },() => {
            this.checkValidity()
        })
    }
  
    getConditionData = (value,valid) => { 
        this.setState({
            data: {
                ...this.state.data,
                condition: value
            }  
        },() => {
            this.checkValidity()
        })
    }
  
    getPriceData = (value,valid) => { 
        this.setState({
            data: {
                ...this.state.data,
                price: value
            }  
        },() => {
            this.checkValidity()
        })
    }
    
    getNegotiableData = (value,valid) => { 
        this.setState({
            data: {
                ...this.state.data,
                pricenegotiable: value
            }  
        },() => {
            this.checkValidity()
        })
    }

    saveData = () => {
        const {data} = this.state;
        this.props.saveData(data, 1);
        this.props.stepCtr(2);
    }
  render() {
    return (
        <Container>
            <Navigation />
            <Steps step="1" />
            <Row padding="0 86px"  bottomspacing="80px">
                <Box height="auto" width="100%">
                    <Column justify="space-between" padding="40px 86px">
                        <Row bottomspacing="20px" justify="space-between" height="400px" align="space-between" >
                            <Form
                                width="30%"
                                placeholder="Select Make"
                                label="Make"
                                type="select"
                                options={["Toyota"]}
                                data={this.getMakeData}
                            />
                            <Form
                                width="30%"
                                placeholder="Select Model"
                                label="Model"
                                type="select"
                                options={["Corolla"]}
                                data={this.getModelData}
                            />
                            <Form
                                width="30%"
                                placeholder="Select Series"
                                label="Series"
                                type="select"
                                options={["2.0 Sedan"]}
                                data={this.getSeriesData}
                            />
                        
                            <Form
                                width="30%"
                                placeholder="Select Year"
                                label="Year"
                                type="select"
                                options={["2008","2009","2010","2011","2012","2013","2014","2015","2016","2017"]}
                                data={this.getYearData}
                            />
                        
                            <Form
                                width="30%"
                                placeholder="Select Milleage"
                                label="Milleage (km)"
                                type="select"
                                options={["High", "Low"]}
                                data={this.getMilleageData}
                            />
                            <Form
                                width="30%"
                                placeholder="Select Body Type"
                                label=" Body Type"
                                type="select"
                                options={["Convertibles","Coupe","Crossover","Hatchback","Luxury","MUV","Sedan","Notchback", "SUV","Wagon","Van","MiniVan"]}
                                data={this.getBodyTypeData}
                            />
                        
                            <Form
                                width="30%"
                                placeholder="Select Transmission"
                                label="Transmission Type"
                                type="select"
                                options={["Traditional Automatic","Automated Manual","Continuously Variable","Dual-Clutch","Tiptronic","Direct Shift Gearbox"]}
                                data={this.getTransTypeData}
                            />
                            <Form
                                width="30%"
                                placeholder="Select Condition"
                                label="Condition"
                                type="select"
                                options={["Good","Bad"]}
                                data={this.getConditionData}
                            />
                        
                            <Form
                                width="30%"
                                placeholder="Select Price"
                                label="Price"
                                type="select"
                                options={["$20000","$30000","$45000"]}
                                data={this.getPriceData}
                            />
                        
                            <Form
                                width="50%"
                                label="Price is Negotiable"
                                type="checkbox"
                                data={this.getNegotiableData}
                            />
                        </Row>
                        <Row justify="space-between">
                            <ButtonSecondary width="20%" height="58px" onClick={()=>{}}> Previous</ButtonSecondary>
                            <Button width="20%" height="58px" style={{background: this.state.disabled? "#5ac9f7" : null}} onClick={this.saveData} disabled={this.state.disabled}> Next </Button>
                        </Row>
                </Column>
                
                </Box>
            </Row>       
        </Container>
    );
  }
}