import React, {Component} from 'react';
import {Box, Row, Column} from '../../styles/init';
import styled from 'styled-components';

export default class Package extends Component {


  render() {
      let content;
      this.props.feature ?
            content = this.props.feature.map((feature, index) => {
              return (
                <li key={index}>{feature}</li>
              )
              
            }) : content ="&nbsp"

  

    return (
            <Box height="300px" width ="300px" padding="30px 10px 10px 20px">
              <Column height="100%">
                <Column justify="center" bottomspacing="30px">
                  <h2>{this.props.type}</h2>
                  <p className="text-grey">"{this.props.category}"</p>
                </Column>
                <Row justify="center">
                  <h1 className="smallheader">{this.props.amount}</h1>
                </Row>

                <Row justify="space-between" align="flex-end" style={{flexGrow: 1}}>
                  <List>
                    {content}
                  </List>

                  <Check width="35px" bottomspacing="0" height="40px" >
                    <input type="radio" id={this.props.type} name="package" value={this.props.value} onChange={this.props.data} />
                    <label htmlFor={this.props.type}>
                      <span className="inside"></span>
                    </label>                  
                  </Check>
                </Row>
              </Column>
            </Box>
    )
  }
}

const List = styled.ul `
 list-style: none;
 padding: 10px;
 margin: 0;

 li {
   font-size: 14px;
   color: rgb(90, 88, 88);
 }

`

const Check = styled.div `
width: ${props=>props.width};
height: ${props=>props.height};
border-radius: 8px;
background: white;
padding: ${props=>props.padding};
box-sizing: border-box;
border: 1px solid #D8D8D8;
box-shadow: 0 2px 42px 0 rgba(232,232,232,0.50);
overflow: hidden;

input {
  display: none
}

label {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

input:checked + label .inside {
  background-image: linear-gradient(to right, #008DFF, #0257B9);
  height: 80%;
  width: 80%;
  box-shadow: 0 2px 42px 0 rgba(232,232,232,0.50);
  border-radius: 8px;
}

`