import React, {Component} from 'react';
import {connect} from 'react-redux';
import { withRouter } from "react-router";

import Navigation from '../../../reuseables/navigation';
import Steps from '../../../reuseables/steps';
import { Container, Box, Row, Column, Button } from '../../../styles/init';
import Package from '../../..//reuseables/package';
import {SellCar} from '../../../redux/actions/sellcar';

class StepFour extends Component {
  constructor(props) {
    super(props)
    this.state = {
      packagetype: "",
      disabled: true
    }
  }

  checkValidity = () => {
    if(this.state.packagetype){
        this.setState({
            disabled: false
        })
    }
  }

  getType = (e) => {
    this.setState({
      packagetype: e.target.value
    }, () => {
      this.checkValidity();
    })
  }

  onSubmit = () => {
    if(localStorage.getItem("ax_auth")){
      const data = {...this.props.getData(), ...this.state}
      this.props.SellCar(data)
    }else{
      this.props.history.push("/login")
    }
  }
  
  componentDidUpdate() {
    const { payload } = this.props;
    if (payload.Statuscode === "100") {
        this.props.stepCtr(5)
    }
  }

  render() {
    const {loading, payload} = this.props
    return (
      <Container>
        <Navigation />
        <Steps step="4" />
        <Row padding="0 86px"  bottomspacing="80px">
          <Box height="auto" width="100%">
            <Column justify="space-between" padding="40px 86px">
              <Row  bottomspacing="20px" >
                <Row bottomspacing="17px" justify="center">
                  <p style={{color: "red"}}>{payload ? payload.Message : ''}</p>
                </Row>
                <Row bottomspacing="60px" justify="center">
                    <h2>Choose a Package</h2>
                </Row>
            
                <Row justify="space-evenly" padding="0 60px" bottomspacing="20px">
                  <Package
                    type="Gold Package"
                    value="Gold" 
                    category="Quick Sell"
                    amount="$ 50"
                    feature={["Priority Ad", "Top featured ad for 7 days", "Online for 30 days", "50 times more views"]}
                    data={this.getType}
                    />

                  <Package
                    type="Standard Package"
                    value="Standard" 
                    category="Standard Ad"
                    amount="$ 20"
                    feature={["Standard Ad", "Online for 30 days"]}
                    data={this.getType}
                    />
                </Row>
              </Row>
              <Row justify="space-between">
                <Button width="20%" height="58px" onClick={() => this.props.stepCtr(3)}> Previous </Button>
                <Button width="20%" height="58px" style={{background: this.state.disabled? "#5ac9f7" : null}} disabled={this.state.disabled || loading} onClick={this.onSubmit}>{loading ? <div className="loader"></div> : `Submit`}</Button>
              </Row>
            </Column>
          </Box>
        </Row>       
      </Container>
    );
  }
}

const mapStateToProps = state => {
  return {
      payload: state.Sellcar.sell_payload,
      loading: state.Sellcar.sell_car_loading,
      success: state.Sellcar.sell_car_successful,  
  }
}

export default withRouter(connect(mapStateToProps, {SellCar})(StepFour));