import * as Types from '../types';
import API from '../../default';
import {getLoginSalt} from '../../hash';

export const SellCar = (data) => {
  const hash = getLoginSalt('','');
  const token = localStorage.getItem("ax_auth");
  return dispatch => {
    dispatch({
      type: Types.SELL_CAR,
      payload: API({
        method: 'post',
        url: `/?end=sellcars`,
        data: {
            ...data,
            "hashpass": hash,
            "accesstoken": token
        }
      })
    })
  }
}