import React, {Component} from 'react';
import { Route, Switch } from 'react-router-dom';

import StepOne from './steps/step-1';
import StepTwo from './steps/step-2';
import StepThree from './steps/step-3';
import StepFour from './steps/step-4';
import StepFive from './steps/step-5';


export default class SellCar extends Component {

  constructor() {
    super()
    this.state = {
      stepOne: {},
      stepTwo: {},
      stepThree: {},
    }

  }

  stepCtr = (step) => {
    this.props.history.push(`${this.props.match.path}/step${step}`)
  }

  saveData = (data, step) => {
    switch(step) {
      case 1:
      this.setState({
        ...this.state,
        stepOne: {
          ...data
        }  
      });
      break;
      case 2:
      this.setState({
        ...this.state,
        stepTwo: {
          ...data
        }  
      });
      break;
      case 3:
      this.setState({
        ...this.state,
        stepThree: {
          ...data
        }  
      });
      break;
      case 4:
      this.setState({
        ...this.state,
        stepFour: {
          ...data
        }  
      });
      break;
      default:
      break;
    } 
  }

  getData = () => {
    const data = {...this.state.stepOne, ...this.state.stepTwo, ...this.state.stepThree}
    return data;
  }

 render() {
  return (

          <Switch>
            <Route exact path={this.props.match.path} render={() => <StepOne  stepCtr={this.stepCtr}
                                                                              saveData={this.saveData} />} />
            <Route exact path="/sell/step2" render={() => <StepTwo  stepCtr={this.stepCtr}
                                                                    saveData={this.saveData} />} />
            <Route exact path="/sell/step3" render={() => <StepThree  stepCtr={this.stepCtr}
                                                                      getData={this.getNameData}
                                                                      saveData={this.saveData} />} />
            <Route exact path="/sell/step4" render={() => <StepFour stepCtr={this.stepCtr}
                                                                    getData={this.getData} />} />
            <Route exact path="/sell/step5" render={() => <StepFive/>} />
          </Switch>
  )
 }
}