import React, {Component} from 'react';
import Navigation from '../../../reuseables/navigation';
import Steps from '../../../reuseables/steps';
import { Container, Box, Row, Column, Button} from '../../../styles/init';
import Form from '../../../reuseables/form';

export default class StepThree extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: {
        fullname: "",
        email: "",
        countrycode: "",
        phone: "",
        city: "",    
      },
      disabled: true 
    }
  }

  checkValidity = () => {
    let valid = [];
    Object.keys(this.state.data).map(key => {
            this.state.data[key] ? valid.push(true) : valid.push(false);
    });
    const isvalid = valid.every(item => item);
    isvalid ?  this.setState({disabled: false}) : this.setState({disabled: true});
  }

  getEmailData = (value,valid) => { 
      this.setState({
        data: {
          ...this.state.data,
          email: value
        }   
      },() => {
        this.checkValidity()
      });
  }

  getNameData = (value,valid) => { 
    this.setState({
      data: {
        ...this.state.data,
        fullname: value
      }   
    },() => {
      this.checkValidity()
    });
  }

  getPhoneNumberData = (value,valid) => { 
      this.setState({
        data: {
          ...this.state.data,
          phone: value
        }   
      },() => {
        this.checkValidity()
      });
  }

  getCityData = (value,valid) => { 
    this.setState({
      data: {
        ...this.state.data,
        city: value
      }   
    },() => {
      this.checkValidity()
    });
  }

  getCountryCodeData = (value,valid) => { 
    this.setState({
      data: {
        ...this.state.data,
        countrycode: value
      }   
    },() => {
      this.checkValidity()
    });
  }

  saveData = () => {
    const {data} = this.state;
    this.props.saveData(data, 3);
    this.props.stepCtr(4);
  }

  render() {
    return (
      <Container>
        <Navigation />
        <Steps step="3" />
        <Row padding="0 86px"  bottomspacing="80px">
          <Box height="auto" width="100%">
            <Column justify="space-between" padding="40px 86px">
              <Row bottomspacing="20px" padding="30px 0 20px" justify="space-between" height="400px" align="space-between">
                <Form
                  width="45%"
                  placeholder=""
                  label="Name"
                  type="text"
                  data={this.getNameData}
                />
                <Form
                  width="45%"
                  placeholder=""
                  label="Email"
                  type="email"
                  data={this.getEmailData}
                />
                <Form
                  width="45%"
                  placeholder=""
                  label="Country Code"
                  type="select"
                  options={["Nigeria (+234)"]}
                  data={this.getCountryCodeData}
                />
                <Form
                  width="45%"
                  placeholder=""
                  label="Phone Number"
                  type="text"
                  data={this.getPhoneNumberData}
                />
                <Form
                  width="45%"
                  placeholder=""
                  label="Area/City"
                  type="select"
                  options={["Lagos"]}
                  data={this.getCityData}
                />
              </Row>
              <Row justify="space-between">
                <Button width="20%" height="58px" onClick={() => this.props.stepCtr(2)}> Previous</Button>
                <Button width="20%" height="58px"  style={{background: this.state.disabled? "#5ac9f7" : null}} onClick={this.saveData} disabled={this.state.disabled}> Next </Button>
              </Row>
            </Column>
          </Box>
        </Row>       
      </Container>
    );
  }
}